@mixin transition($value) {
    transition: $value;
    -moz-transition: $value;
    -o-transition: $value;
    -webkit-transition: $value;
}

.calendar {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    & .calendar__arrow {
        width: 10%;
        display: flex;
        justify-content: center;
        & button {
            background: transparent;
            border: 1px solid rgb(241, 163, 163);
            width: 30px;
            height: 30px;
            align-self: center;
            @include transition(0.3s);
            &:active, &:hover{
                outline: none;
                background-color: rgb(241, 163, 163);
                color: white;
            }
            &:focus {
                outline: none;
            }
            &:active{
                transform: scale(.9);
            }
        }
    }
    & .calendar__year {
        font-size: 2em;
        width: 80%;
    }
    & .calendar__month {
        width: 80%;
        font-size: 1.4em;
        margin-top: 12px;
    }
    & .calendar__days {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
    }
    & .calendar__day {
        width: calc(100% / 7);
        box-sizing: border-box;
        height: 50px;
        border: 1px solid lightcoral;
        line-height: 50px;
        @include transition(0.3s);
        &:hover {
            background-color: rgb(241, 163, 163);
            color: white;
        }
        &_current {
            background-color: lightcoral;
            color: white;
        }
        &_prev {
            color: #9c9c9c;
        }
    }
}

.calendar__load {
    height: 50px;
    width: 50px;
    margin: auto;
    border-radius: 50%;
    border: 1px solid black;
    position: relative;

    &::after {
        content: "";
        width: 10px;
        height: 10px;
        display: block;
        position: absolute;
        background: black;
        border-radius: 50%;
        top: 0;
        left: calc(50%);
        transform-origin: 0 25px;
        animation: 2s infinite calendar_load ease-in-out alternate-reverse;
    }
}
@keyframes calendar_load {
    0% {
        transform: rotate(50deg);
    }
    100% {
        transform: rotate(290deg);
    }
}
